<template>
<div id="backstage_index">
  <div class="hdie">
    <div class="ure_toux">
      <div class="toux">
        <span>K</span> <span>{{user.phone}}</span>
      </div>
    </div>
    <div class="data_box">
      <div class="tix_data_box">
        <img src="/static/icon/2.png" class="yue_qianb">
        <div><span>{{user.balance_data.balance}}</span> <span>账户余额</span>
        </div>
        <div class="tx">提现</div>
        <img src="/static/img/toubm.png" class="toubm">
      </div>
    </div>
    <div class="duox_box">
      <div>
        <span v-if="today">{{statistics_today.total_money}}</span>
        <span v-else>{{statistics_yesterday.total_money}}</span>
        <span v-if="today">今日订单金额</span>
        <span v-else>昨日订单金额</span>
        <img src="/static/icon/3.png" @click="today = today ? false : true">
      </div>
      <div>
        <span v-if="today">{{statistics_today.total_count}}</span>
        <span v-else>{{statistics_yesterday.total_money}}</span>
        <span v-if="today">今日订单量</span>
        <span v-else>昨日订单量</span>
      </div>
      <div>
        <span v-if="today">{{statistics_today.total_success_count}}</span>
        <span v-else>{{statistics_yesterday.total_success_count}}</span>
        <span v-if="today">今日成功订单</span>
        <span v-else>昨日成功订单</span>
      </div>
      <div>
        <span v-if="today">{{statistics_today.total_success_money}}</span>
        <span v-else>{{statistics_yesterday.total_success_money}}</span>
        <span v-if="today">今日成功金额</span>
        <span v-else>昨日成功金额</span>
      </div>
    </div>
  </div>
  <div class="echarts_box">
      <div data-v-16175d64="" id="echarts">
        <ve-line :data="chartData" :settings="chartSettings"  :extend="charts" :loading="chartsloading" style="width:784px; height: 370px; -webkit-tap-highlight-color: transparent; user-select: none; position: relative;" id="myChart"></ve-line>
        <span data-v-16175d64="" class="xy_name">日期</span>
        <span data-v-16175d64="" class="xy_name">订单数</span>
        <span data-v-16175d64="" class="xy_name">金额</span>
        <div data-v-16175d64="" class="tiet_box"><span data-v-50c5b756="" class="tite">订单统计</span> </div>
      </div>
  </div>
</div>
</template>

<script>
  export default {
    data () {
      this.chartSettings = {
        axisSite: { right: ['提交金额'] },
        xAxisType: 'category',
        yAxisType: ['normal', 'normal'],
      }
      return {
        chartData: {
          columns: ['日期', '提交订单', '提交金额','成功订单','成功金额'],
          rows:''
        },
        charts:{
          backgroundColor: 'rgb(255, 255, 255)',
          textStyle:{
             color:'rgb(104, 114, 140)',
             fontFamily: "Microsoft YaHei",
             fontSize:10,
             lineHeight:25
          },
          xAxis:{
            splitLine:{
              show:true,
              lineStyle:{
                 color:['rgba(223, 224, 232, 0.9)'],
                 type:'dashed'
              }
            }
          },
          yAxis:{
            splitLine:{
              show:true,
              lineStyle:{
                 color:['rgba(223, 224, 232, 0.9)'],
                 type:'dashed'
              }
            }
          },
          tooltip:{
             padding: 10,
             backgroundColor:'rgb(255, 255, 255)',
             borderColor:'rgb(51, 51, 51)',
             textStyle:{
              color:'rgb(104, 114, 140)',
              fontFamily:'Microsoft YaHei',
              fontSize:12
             },
             extraCssText: 'box-shadow: rgba(80, 125, 250, 0.06) 0px 4px 8px 0px;border-radius: 5px;line-height:25px;'
          },
          legend:{
              data: [{
                  name: '提交订单',
                  icon: 'roundRect',
                  textStyle: {
                      color: '#a2a9ba'
                  }
              },{
                  name: '提交金额',
                  icon: 'roundRect',
                  textStyle: {
                      color: '#a2a9ba'
                  }
              },{
                  name: '成功订单',
                  icon: 'roundRect',
                  textStyle: {
                      color: '#a2a9ba'
                  }
              },{
                  name: '成功金额',
                  icon: 'roundRect',
                  textStyle: {
                      color: '#a2a9ba'
                  }
              }],
              backgroundColor:'rgb(255, 255, 255)',
              borderColor:'rgb(51, 51, 51)',
          }
        },
        user:{
          phone:"",
          balance_data:{
            balance:"0.00"
          }
        },
        chartsloading:true,
        statistics_today:"",//今日
        statistics_yesterday:"",//昨日
        today:true,
        spin:false
      }
    },
    mounted(){
        this.getstatistics(7);
        this.getinfo();
    },
    methods: {
         getinfo(){
           if(localStorage.getItem('user_info')){
             var user_info = JSON.parse(localStorage.getItem('user_info'));
             if(user_info.time < new Date().getTime()){
               this.ajaxuserinfo();
             }else{
               this.user = user_info.data;
             }
           }else{
             this.ajaxuserinfo();
           }
         },
         ajaxuserinfo(){
           this.$http.get('api/user/userinfo',{}, res => {
                if (res.status == 200) {
                    if(res.data.code == 200){
                      var userinfo = {};
                          userinfo.time = new Date().getTime() + 10000;
                          userinfo.data = res.data.data;
                      localStorage.setItem('user_info',JSON.stringify(userinfo));
                      this.user = userinfo.data;
                    }else if(res.data.code == 201){
                      this.$Message.warning(res.data.msg);
                    }
                } else {
                   console.log(res.message);
                }
           });
         },
         getstatistics(time){
           if(localStorage.getItem('user_statistics_'+time)){
             var statistics = JSON.parse(localStorage.getItem('user_statistics_'+time));
             if(statistics.time < new Date().getTime()){
               this.ajaxstatistics(time);
             }else{
               this.chartData.rows = statistics.data.chart;
               this.chartsloading = false;
               this.statistics_today = statistics.data.Today;
               this.statistics_yesterday = statistics.data.Yesterday;
             }
           }else{
             this.ajaxstatistics(time);
           }
         },
         ajaxstatistics(time){
            this.$http.post('api/user/statistics',{time:time}, res => {
                 if (res.status == 200) {
                     if(res.data.code == 200){
                       var local = {};
                           local.time = new Date().getTime() + 50000;
                           local.data = res.data.data;
                       localStorage.setItem('user_statistics_'+time,JSON.stringify(local));
                       this.chartData.rows = local.data.chart;
                       this.chartsloading = false;
                       this.statistics_today = local.data.Today;
                       this.statistics_yesterday = local.data.Yesterday;
                     }else if(res.data.code == 201){
                       this.$Message.warning(res.data.msg);
                     }
                 } else {
                    console.log(res.message);
                 }
            });
         }
    }
  }
</script>



